import { Basket } from '@/state/atoms/Basket';
import React, { FC, useState } from 'react'
import { useRecoilState, useRecoilValue } from 'recoil';
import { BasketLength, BasketPrice } from '@/state/selectors/Basket';
import { splitPrice } from '@/service';
import { useNavigate } from 'react-router-dom';
import { Controller, SubmitHandler, useForm } from 'react-hook-form';
import InputMask from 'react-input-mask';
import { useOrder } from '@/adapters/orders';

interface Props{
    setOpen: (open: boolean) => void
}

interface IFormInput {
    first_name: string;
    last_name: string;
    email: string;
    phone: string;
    city: string;
    address: string;
    index: string;
    checkbox?: boolean;
  }


const BasketComponent: FC<Props> = ({setOpen}) => {

    const [basket, setBasket] = useRecoilState(Basket)
    const basketPrice = useRecoilValue(BasketPrice)

    const handleDelete = (item: any) => {
        setBasket(basket => basket.filter(_item => _item.type.id != item.type.id))
    }

    return (
        <div className="wrapper" style={{flex: 1}}>
            <div className="basket__title-mobile"></div>
        <div className="name">
            <div className="name__title">
                <div className="name__product"></div>
                <div className="name__sale"></div>
                <div className="name__quantity"></div>
                <div className="name__price"></div>
                <div className="name__sum"></div>
            </div>
        </div>
        <div className="divider"></div>
        <div className="total">

            {basket.map(_item => (
            <div key={_item.type.id}>
                <div className="total__subtitle">

                    <div className="total__xmark" onClick={() => handleDelete(_item)}><img src="icons/emblem/xmark.svg"/></div>

                    <div className="total__product">{_item.type.product_title}</div>

                    <div className='total__field'>
                        <div className="total__field-title">:</div>
                        <div className="total__sale total__value">0<span>%</span></div>
                    </div>

                    <div className='total__field'>
                        <div className="total__field-title">:</div>
                        <div className="total__quantity total__value">{_item.quantity} .</div>
                    </div>

                    <div className='total__field'>
                        <div className="total__field-title">:</div>
                        <div className="total__price total__value">{splitPrice(_item.type.price)} </div>
                    </div>

                    <div className='total__field total__summ'>
                        <div className="total__field-title ">:</div>
                        <div className="total__sum total__value">{splitPrice(_item.total_product)} </div>
                    </div>
                </div>
                <div className="divider"></div>
            </div>
            
            ))}
            
        </div>
            
            <div className="result">
                <div className='result__total-price'>
                    <div className="result__name">: </div>
                    <div className="result__total">{splitPrice(basketPrice)}<span> </span></div>
                </div>
                <button className="result__btn" onClick={() => setOpen(true)}></button>
            </div>
    </div>
    )
}


const EmptyBasket:FC  = () => {

    const navigate = useNavigate()

    return (
        <>
        <div className="basket" style={{flex: 1}}>
            <div className="basket__title">
                       ,        
            </div>
            <button className="basket__price" onClick={() => navigate('/catalog')}> </button>
        </div>
        </>
    )
}


const Form: FC<Props>  = ({setOpen}) => {

    const { isLoading, mutateAsync} = useOrder()
    const [basket, setBasket] = useRecoilState(Basket)
    const basketValue = useRecoilValue(BasketPrice)

    const {
        register,
        control,
        reset,
        formState: {
            errors,
        },
        handleSubmit
    } = useForm<IFormInput>()

    const onSubmit: SubmitHandler<IFormInput> = async (data) => {
        delete data['checkbox']
        await mutateAsync(
            {
                ...data,
                order: basket,
                total: basketValue
            }
        )
        reset()
        setBasket([])
        setOpen(false)
    }

    return (
        <form className="form" onSubmit={handleSubmit(onSubmit)} style={{flex: 1}}>
            <div className="form__wrapper">
                    <div className="form__title"> </div>

                    <input placeholder="" {...register("first_name",{
                        required: '    '
                    })}/>
                    { errors?.first_name && <p>{errors?.first_name?.message || 'Error' }</p>}

                    <input placeholder="" {...register("last_name",{
                        required: '    '
                    })}/>
                    { errors?.last_name && <p>{errors?.last_name?.message || 'Error' }</p>}

                    <input placeholder="Email" type="email" {...register("email",{

                    })}/>
                    { errors?.email && <p>{errors?.email?.message || 'Error' }</p>}

                    <InputMask  mask="+7(999)-999-9999" placeholder='' {...register("phone",{
                        required: '    ',
                        validate: value => value.includes('_') === false || '   ',
                        })} />

                    { errors?.phone && <p>{errors?.phone?.message || 'Error' }</p>}

                <div className="form__subtitle">: </div>

                    <input placeholder="" type="text" {...register("city",{
                        required: '    '
                    })}/>
                    { errors?.city && <p>{errors?.city?.message || 'Error' }</p>}

                    <input placeholder="" type="text" {...register("address",{
                        required: '    '
                    })}/>
                    { errors?.address && <p>{errors?.address?.message || 'Error' }</p>}

                    <input placeholder="" type="number" {...register("index",{
                        required: '    '
                    })}/>
                    { errors?.index && <p>{errors?.index?.message || 'Error' }</p>}

                <div className="form__checkbox">
                    <input type='checkbox' {...register('checkbox', {
                        validate: value => value === true || '     ',
                    })} />
                    
                    <span>    </span>
                </div>
                { errors?.checkbox && <p>{errors?.checkbox?.message || 'Error' }</p>}
                <button className="form__btn" type='submit'></button>
            </div>
        </form>
    )
}


const BasketPage:FC  = () => {

    const [ open, setOpen ] = useState<boolean>(false)
    const basketLength = useRecoilValue(BasketLength)

    return basketLength > 0 ? (
        open ? (<Form setOpen={setOpen}/>) : (<BasketComponent setOpen={setOpen}/>)
    ) : (
        <EmptyBasket/>
    )
}

export default BasketPage
